// main: ../style.less

.about__inner {
  display: flex;
  max-width: 1090px;
}

.about__photo,
.about__content {
  width: 50%;
}

.about__photo {
  position: relative;

  img {
    position: relative;
    z-index: 2;
  }

  // &:before {
  //   content: "";
  //   width: 360px;
  //   height: 350px;
  //   background-color: #EFEFEF;

  //   position: absolute;
  //   left: 1rem;
  //   bottom: 1rem;
  //   z-index: 1;
  // }
}

.about__content {
  padding-left: 3.2rem;
  margin-top: 2rem;
}

.about__text {
  margin-bottom: 2.8rem;

  font-size: 1rem;
  line-height: 2;
  color: #000;
  font-weight: 300;
}

.about__logo img {
  width: 100%;
  height: auto;
  display: block;
}

.about__social-link {
  padding-left: 10px;
}

@media (max-width: $tablet) {
  .about {
    margin: 4rem 0;
  }

  .about__photo,
  .about__content {
    width: 100%;
  }

  .about__photo {
    order: 2;

    // &:before {
    //     width: 100%;
    //     height: 300px;

    //     left: 0;
    // }

    img {
      position: relative;
      margin: 0 auto;
    }
  }

  .about__content {
    order: 1;
    margin-bottom: 2rem;
    padding-left: 0;

    text-align: center;
    max-width: 600px;
    margin-left: auto;
    margin-right: auto;
  }
}

@media (max-width: $mobile) {
  .about__inner {
    flex-wrap: wrap;
  }

  .about__text {
    font-size: .8rem;

  }
}
